.currencies-line {
    background-color: #191919;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    min-height: 48px;
    border-radius: 8px;

    width: 100%;
}

.currency-button {
    font-weight: 500;
    padding: 8px;

    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 8px;
    white-space: nowrap;

    cursor: pointer;
}

.currency-button:hover {
    background-color: rgba(115, 104, 231, 0.1);
}

.currency-position {
    color: rgb(107, 124, 118);
    font-weight: 600;

    margin: 0 2px 0 0;
}

.currency-name {
    color: rgb(215, 249, 236);
    margin-left: 2px;
}
