.page-container {
    height: 100%;

    overflow-x: hidden;
}

.page-container>header {
    background-color: transparent;
    color: rgb(129, 242, 200);
}

.header {
    background: black;
    height: 100%;
    width: 100%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.header>div>h2 {
    color: rgb(129, 242, 200);
    font-family: 'Poppins', sans-serif !important;

    max-width: 700px;
    text-align: center;

    font-weight: 700;
    font-size: 48px;

    line-height: 1.2;
    opacity: 0.8;
}

@keyframes Hide-Field {
    0% {
        transform: translateY(-50%);
        opacity: 0;
    }

    100% {
        transform: translateY(0%);
        opacity: 1;
    }
}


@keyframes Show-Field {
    0% {
        transform: translateY(0%);
        opacity: 1;
    }

    100% {
        transform: translateY(-50%);
        opacity: 0;
    }
}

.visible {
    animation-duration: .3s;
    animation-timing-function: ease-out;
    animation-name: Show-Field;

    transform: translateY(-50%);
    opacity: 0;
}

.hidden {
    animation-duration: .3s;
    animation-timing-function: ease-out;
    animation-name: Hide-Field;

    transform: translateY(0%);
    opacity: 1;
}

.logo {
    height: 40px;
}

.nsfw-logo {
    height: 320px;
}

@media (max-width: 740px) {
    .search {
        width: 90% !important;
    }

    .nsfw-logo {
        height: 300px;
    }
}

.social-links {
    position: absolute;

    transform: translateX(50%);
    bottom: 12px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.social-links>div>a {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: left;

    text-decoration: none;

    color: white;

    font-size: 14px;
    margin-bottom: 12px;
}

.social-links>div>a>img {
    width: 16px;
    height: 16px;

    margin-right: 6px;
}
