.currency-info-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;

    margin-top: 24px;
}

.currency-info {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.currency-data {
    margin-left: 12px;

    display: flex;
    flex-direction: column;

    justify-content: center;
}

.currency-data>p {
    font-size: 1.2em;
    color: rgb(215, 249, 236);
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
}

.currency-data>div {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.currency-data>div>p {
    margin: 0 10px 0 0;

    font-size: 1.2em;
    color: rgb(160, 255, 219);
    font-family: 'Fira Mono', sans-serif;
    font-weight: 500;
}

.icon {
    width: 50px;
    height: 50px;
}

.swap-icon {
    width: 20px;
    height: 20px;

    border-radius: 50%;

    margin: -16px 0 0 -16px;

    border: 2px solid rgb(26, 20, 51);
}

.other-info {
    display: flex;
    flex-direction: row;

    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;

    margin-left: 128px;

    flex-grow: 1;
}

.other-info>div {
    display: flex;
    flex-direction: column;

    align-items: center;
    justify-content: center;

    color: rgb(215, 249, 236);
    margin: 0 8px;
}

.other-info>div>span {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    opacity: 0.7;
}

.other-info>div>p {
    font-family: 'Fira Mono', sans-serif;
}


.buttons-panel {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
}

.button {
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    border-radius: 8px;

    padding: 3px 9px;

    cursor: pointer;

    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.trade-button {
    composes: button;

    border: 1px solid rgba(129, 242, 200, 0.5);
    color: rgb(129, 242, 200);
}

.contract-button {
    margin-left: 12px;

    composes: button;

    border: 1px solid rgba(115, 104, 231, 0.5);
    color: rgb(115, 104, 231);
}

.contract-button>svg {
    margin-left: 4px;
}

@media (max-width: 1020px) {
    .currency-info-container {
        flex-direction: column;
    }

    .other-info {
        justify-content: center;

        margin: 24px 0 24px 0;
    }
}
