.page-container {
    height: 100%;
}

.page-container>header {
    background-color: black;
    color: rgb(129, 242, 200);
}

.page-container>header>div>:global(.MuiTypography-root) {
    flex-grow: 0 !important;
}

.page-container>header>div>:global(.MuiTextField-root) {
    margin-left: auto !important;
    margin-right: auto !important;
}

.page-content {
    padding: 84px 16px 16px;
}

.search-button {
    display: none !important;
}

@media (max-width: 960px) {
    .search-button {
        margin-left: auto !important;
        display: block !important;
    }

    .search-field {
        display: none !important;
    }
}

.logo {
    height: 40px;
}
