.label {
    font-weight: 400;
    line-height: 1.5;

    font-size: 0.9em;
    padding: 2px 6px;
    min-width: 40px;
    font-family: 'Poppins', sans-serif;
    border-radius: 8px;
}

.label-buy {
    composes: label;

    background-color: rgba(0, 195, 147, 0.075);
    color: rgb(0, 195, 147);
}

.label-sell {
    composes: label;

    background-color: rgba(239, 66, 91, 0.075);
    color: rgb(239, 66, 91);
}
